@import '_mixin';


.app-download{
	max-width: 1200px;
    margin: 145px auto 65px;
	.app-download-inner{
		display: flex;
		.app-d-col {
			flex: 50%;
			position: relative;
		}
		.app-download-content{
			.head-block{
				.head-title {
					color: #46aef7;
					font-size: 65.87px;
					position: relative;
					display: inline-block;
					margin-bottom: 18px;
					&span {
						font-size: 49.4px;
						position: relative;
						top: -7px;
						margin-left: 11px;
					}
				}
				.head-sub {
					color: #ecf0fc;
					font-size: 24.56px;
					font-family: Metropolis;
				}
				.head-text {
					color: #2a2a2a;
					font-size: 14px;
					line-height: 30px;
					max-width: 335px;
					margin: 45px 0 80px;
				}
			}
			
			.app-d-body {
				display: flex;
				.app-d-b-item {
					text-align: center;
					margin-right: 25px;
					.app-d-b-image{
						display: inline-block;
						width: 100%;
						height: 102px;
						position: relative;
					}
					&:nth-child(1){
						.app-d-b-image:before{
							content: '';
							background: url(../img/app-download/qr-code-content.png) no-repeat center center/cover;
							width: 102px;
							height: 102px;
							position: absolute;
							left: 50%;
							top: 50%;
							transform: translate(-50% , -50%);
						}
					}
					&:nth-child(2){
						margin: 0;
						.app-d-b-image:before{
							content: '';
							background: url(../img/app-download/h5.svg) no-repeat center center/cover;
							width: 74px;
							height: 83px;
							position: absolute;
							left: 50%;
							top: 50%;
							transform: translate(-50% , -50%);
						}
					}
					
					.app-d-b-text {
						color: #3b414d;
						font-size: 12px;
					}
					.app-d-b-link {
						font-size: 12px;
						color: #46aef7;
					}
				}
			}
		}
		.app-download-image{
			height: 560px;
			.app-d-image{
				width: 631px;
				height: 591px;
				position: absolute;
				&.left{
					background: url(../img/app-download/pwa1.JPEG) no-repeat center center/cover;
					left: 0;
					top: 41px;
				}
				&.front{
					background: url(../img/app-download/app-img.png) no-repeat center center/cover;
					left: 50%;
					top: 0;
					transform: translateX(-50%);
					z-index: 2;
				}
				&.right{
					background: url(../img/app-download/pwa2.JPEG) no-repeat center center/cover;
					right: 0;
					top: 21px;
					
				}
			}
			
		}
	}
}
