@import '_mixin';
@import '_variables';

.nav {
	background: #fff;
	@include shadow(0 4px 16px rgba(42, 51, 84, .12));
	@include position(fixed, $z-index: 1000, $top: 0, $left: 0, $right: 0);
	&-inner {
		// padding: 21px;
		padding: 0 57px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		.inline{
			&.menu-wrap{
				display: inline-flex;
				width: 100%;
			}
		}
	}
	.logo-wrap {
		a {
			font-size: 0;
			color: transparent;
			display: block;
			width: 132px;
			height: 51px;
			background: url(../img/nav/logo1.png) no-repeat center center/cover;
		}
	}
	.menu-wrap {
		display: flex;
		margin: 0 40px;
		div.nav-item {
			cursor: pointer;
			&:hover {
				.submenu {
					opacity: 1;
					pointer-events: auto;
					@include transform(translateY(0));
				}
				.submenu-wrap:before {
					@include shadow(0 4px 16px rgba(42, 51, 84, .12));
				}
				.submenu--item {
					@include transform(scale(1) translateY(0));
				}
			}
		}
		a, div.nav-item {
			// padding: 6.5px 10px;
			padding: 29.3px 10px;
			display: flex;
			align-items: center;
			white-space: nowrap;
			position: relative;
			span {
				color: $color-black;
				@include font(18px, normal, 23px);
				@include transition(color .2s ease);
			}
			svg {
				fill: #212121;
				margin: 0 0 0 6px;
				@include transition(fill .2s ease);
			}
			&:not(:last-child) {
				margin: 0 20px 0 0;
			}
			&:hover {
				span {
					// color: #16885f;
					color: #46aef7;
				}
				svg {
					// fill: #16885f;
					fill: #46aef7;
				}
			}
			&.active, &:hover{
				span{
					color: #46aef7;
				}
				&:before{
					position: absolute;
					content: "";
					left: 0;
					bottom: 0;
					width: 100%;
					height: 5px;
					background: #46aef7;
				}
			}
		}
	}
	.user-wrap {
		// white-space: nowrap;
		// a {
		// 	text-align: center;
		// 	display: inline-block;
		// 	vertical-align: top;
		// }
		// a:not(:last-child) {
		// 	margin: 0 15px 0 0;
		// }
		position: relative;
		padding-left: 35px;
		padding-right: 60px;
		.user-icon{
			background: url(../img/nav/user-icon.png) no-repeat center center/cover;
			width: 24px;
			height: 24px;
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			&:after {
				position: absolute;
				content: "";
				width: 1px;
				height: 37px;
				background: linear-gradient(180deg, rgba(215, 213, 213, 0.302959) 10%, #d7d5d5 50%, rgba(215, 213, 213, 0.297356) 90%);
				right: -20px;
				top: 50%;
				transform: translateY(-50%);
			}

		}
		.user-avatar-wrap{
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			width: 49px;
			height: 49px;
			.user-avatar{
				background: url(../img/nav/unnamed.png) no-repeat center center/cover;
				width: 27px;
				height: 31px;
				position: absolute;
				z-index: 1;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
			.user-avatar-overlay {
				position: absolute;
				top: 0;
				left: 0;
				width: 49px;
				height: 49px;
				background: #dfdcdc;
				content: "";
				border-radius: 100%;
			}
		}
		.home-pre-login {
			display: flex;
			overflow: hidden;
		}
		.home-pre-fields, .home-pre-button {
			display: inline-flex;
		}
		.home-pre-fields{
			margin-right: 10px;
			.home-pre-input-wrap {
				border: 1px solid #cdcdd5;
				width: 136px;
				margin-right: 10px;
				position: relative;
				&:last-child{
					margin: 0;
					padding-right: 35px;
				}

				input {
					width: 100%;
					padding: 7px;
					font-size: 15px;
				}
				.tick{
					position: absolute;
					font-size: 12px;
					color: #374e69;
					display: block;
					top: 50%;
					transform: translateY(-50%);
					right: 0;
				}
			}
		}
		.home-pre-button{
			
			.button-default{
				display: inline-block;
				text-align: center;
				border-radius: 0;
				padding: 6.8px;
				min-width: 70px;
				background: linear-gradient(90deg, #fd644f, #f28e26);
				margin-right: 8px;
				&:hover{
					background: linear-gradient(90deg, #fd644f, #f28e26);
					opacity: .5;
				}
			}
			.secondary{
				background: #46aef7;
				margin: 0;
				&:hover{
					background: #46aef7 !important;
	
				}
			}
		}

		
	}
	.submenu-wrap {
		height: 80px;
		@include position(fixed, $z-index: -1, $top: 0, $left: 0, $right: 0, $bottom: auto);
		&:before {
			content: '';
			background: #fff;
			height: 80px;
			@include position(fixed, $z-index: 1, $top: 0, $left: 0, $right: 0, $bottom: auto);
			@include transition(box-shadow .2s ease .1s);
		}
		.submenu {
			background: transparentize(#fff, .05) url(../img/sub-menu/bg.png) no-repeat top -355px left calc(50% - 70px);
			pointer-events: none;
			opacity: 0;
			@include transform(translateY(-80px));
			@include position(fixed, $z-index: -1, $top: 80px, $left: 0, $right: 0, $bottom: auto);
			@include transition(opacity .2s ease, transform .2s ease);

			.submenu-qr-code-item {
				position: absolute;
				right: -150px;
				top: 50%;
				transform: translateY(-50%);
				text-align: center;
				.submenu-qr-image {
					background: url(../img/sub-menu/qr-code-menu.png) no-repeat center center/cover;
					width: 102px;
					height: 102px;
					display: inline-block;
					margin-bottom: 11px;
				}
				.submenu-qr-text {
					font-size: 12px;
					color: #3b414d;
				}
				.submenu-qr-link {
					font-size: 12px;
					color: #46aef7;
				}
				
			}
		}
	}
	.submenu--inner {
		width: 1200px;
		margin: 0 auto;
		position: relative;
		&.item--体育博彩 {
			width: 1310px;
			padding-left: 82px;
		}
	}
	.user-block {
		min-width: 350px;
		max-width: 350px;
		max-height: 80px;
		// border-left: 1px solid #dfebef;
		padding: 17.5px 9px 17.5px 30px;
		.user-links{
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin: 0 0 5px;
			.user-name {
				padding: 0 5px 0 0;
				min-width: 0;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				@include font(15px, normal, 20px);
				color: #1d2e42;
			}
			.links {
				color: #2a2a2a;
				white-space: nowrap;
				@include font(16px, normal, 20px);
				a:not(:last-child) {
					margin: 0 30px 0 0;
				}
			}
		}

		.user-balance {
			// display: flex;
			// align-items: center;
			// justify-content: space-between;
			text-align: right;
			.user-balance-amount {
				color: #55687d;
				@include font(12px, normal 20px);
				display: inline-block;
				border-right: 1px solid #d7d7d7;
				margin-right: 10px;
				padding-right: 20px;
				i {
					font-style: normal;
					// color: #16885f;
					font-size: 14px;
					color: #fc686f;
					background: linear-gradient(-46deg, #fc686f, #ff934c 72%);
					background-clip: text;
					-webkit-background-clip: text;
					-webkit-text-fill-color: transparent;
				}
			}
			button.logout-button {
				// color: #16885f;
				color: #595959;
				@include font(12px, normal, 20px);
				display: inline-block;
			}
		}
		

		
	}
}

@media screen and (max-width: 1486px) {

	.nav {
		.nav-inner {
			// padding-right: 0;
			padding: 0 10px;
		}
		.menu-wrap {
			// display: none;
			flex: 1;
			justify-content: space-between;
			margin: 0 20px;
			a:not(:last-child),
			div.nav-item:not(:last-child) {
				margin: 0;
			}
			@media screen and (max-width: 1292px) {
				display: none;
			}
		}
		.user-block {
			padding-right: 21px;
			padding-left: 21px;
		}
		.submenu--inner{
			&.item--体育竞赛{
				width: 1200px;
				margin-left: 80px;
				.submenu .submenu-qr-code-item{
					right: -135px;
				}
			}
			width: 1100px;
			
			
		}
	}

}

